export default{
  props:{
    tabBars: Array,
    tabIndex: Number
  },
  methods: {
    // tab点击切换
    tabtap(idx){
      // this.tabIndex = idx
      this.$emit('tabtap',idx)
    },
    // tab滑动切换
    tabChange(e){
      // console.log(e.detail.current);
      this.tabIndex = e.detail.current
    }
  }
}
